<template>
  <div class="onlineExamIndex">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack"></Header>
    <div class="onlineExam">
      <img src="../../../static/images/examEnter.jpg" style="width:100%">
    </div>
  </div>
</template>
<script>
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return{
        title:'在线考试'
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      choiseffclTab (index){
        this.defaultIndex =index
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var clientWidth =document.documentElement.clientWidth
      if(clientWidth>=750){
        clientWidth =$('body').width()
      }
    }
  })
</script>
<style scoped>
  body,html{
    width: 100%;
    height: 100%;
  }
  .onlineExam{
    width: 100%;
    height: 100%;
    background-color: #f9f5f5;
    background-image: url("../../../static/images/onlineExam.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    margin-top: 0.8rem;
  }
  .into{
    margin-top: 2.75rem;
    margin-left: 0.52rem;
    margin-right: 0.52rem;
    height: 9rem;

    position: relative;

  }
  .into:after{
    content: '';

  }
  .text{
    overflow: hidden;
    z-index: 2;
    position: absolute;
    left: 0;
    top:0;
    background: white;
    border-radius: 0.18rem;
    -webkit-border-radius: 0.18rem;
    height: 100%;
    width: 100%;

  }
  .text>p{
    padding-left: 0.95rem;
    color:#000;
    font-size: 0.32rem;
    font-weight: bold;
    margin-top: 0.6rem;
    position: relative;
    padding-bottom: 0.2rem;
  }
  .text>p:after{
    position: absolute;
    content: '';
    left: 0.4rem;
    top:0;
    width: 0.44rem;
    height: 100%;
    background-image: url("../../../static/images/star.png");
    background-size: 0.45rem 0.41rem;
    background-repeat: no-repeat;
  }
  .text div p{
    color: #8e8d8d;
    font-size: 0.3rem;
    font-weight: bold;
    padding-left: 0.5rem;
    line-height: 1.5;
  }

  .text div p.img{
    text-align: center;
    font-size: 0.3rem;
    font-weight: bold;
    color: #363636;
    padding-left: 0;
  }
  .text div p img{
    width: 3.44rem;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
  }
  .blank{
    position: absolute;
    left:0.19rem ;
    right:0.19rem;
    bottom:-0.34rem;
    height: 0.68rem;
    border-radius: 0.14rem;
    background: linear-gradient(to bottom,#f9f1f1, #f9f5f5);
    box-shadow: 0 3px 5px #f8edec;
  }
</style>



