                $('.swiper-pagination')
                  .find('span')
                  .eq(activeIndex)
                  .addClass('swiper-pagination-bullet-active')
                  .siblings()
                  .removeClass('swiper-pagination-bullet-active');
              }
            }
          })
          swiper.slideTo(index,0,false)
