    top:0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.2);
  }
  .detailBanner div .p1{
    padding: 0 0.32rem;
    font-size: 0.32rem;
    color: white;
    font-weight: bold;
    padding-top: 0.5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .detailBanner div .p2{
    padding: 0 0.32rem;
    font-size: 0.24rem;
    color: white;
    padding-top: 0.2rem;
    text-indent: 2em;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
  }
  .detailBanner div .p3{
    position: absolute;
    left: 0;
    width: 100%;
    bottom: 0.26rem;
    color: white;
    font-size: 0.24rem;
    text-align: center;
  }
  .albumDetailContent{
    padding: 0 0.32rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .albumDetailContent div {
    width: 3.22rem;
    height: 2.06rem;
    margin-top: 0.3rem;
  }
  .albumDetailContent div img{
    display: inline-block;
    width: 3.22rem;
    height: 2.06rem;
    border-radius: 0.06rem;
    -webkit-border-radius: 0.06rem;
  }
  .albumScan{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: rgb(0,0,0);
    z-index: 989999;
    display: none;
  }
  .swiper-container{
    height: 200px;
    width: 100%;
    position: absolute;
    left:0;
    top:0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
  .swiper-container .swiper-slide img{
    display: block;
    margin: 0 auto;
  }

  .swiper-pagination-bullet-active{
    opacity: 1;
    background: #e83329;
  }
</style>



