<template>
  <div class="heart">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack"></Header>
    <div class="heartBanner">
      <img src="../../../static/images/heart.jpg" alt="">
    </div>
    <div class="haertContent">
      <div class="heartItem">
        <div class="heartInner">
          <div class="heartList">
            <img src="../../../static/images/heartList1.jpg" alt="">
            <div class="heartRight">
              <p class="name">程立波</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                "一颗红心向着党"
              </p>
            </div>
          </div>
          <div class="heartList">
            <div class="heartRight">
              <p class="name">吴文根</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                “做合格党员，当时
                代先锋”
              </p>
            </div>
            <img src="../../../static/images/heartList2.jpg" alt="">
          </div>
          <div class="heartList">
            <img src="../../../static/images/heartList3.jpg" alt="">
            <div class="heartRight">
              <p class="name">吕长军</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                “听党话，感党恩，跟
                党走”
              </p>
            </div>
          </div>
          <div class="heartList">
            <div class="heartRight">
              <p class="name">谭可可</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                “跟着党，心向阳
                不忘初心，砥砺前行”
              </p>
            </div>
            <img src="../../../static/images/heartList4.jpg" alt="">
          </div>
          <div class="heartList">
            <img src="../../../static/images/heartList5.jpg" alt="">
            <div class="heartRight">
              <p class="name">刘文晓</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                “心向群众，砥砺前行”
              </p>
            </div>
          </div>
          <div class="heartList">
            <div class="heartRight">
              <p class="name">王林林</p>
              <p class="joinTime">2011年12月入党</p>
              <p class="position">中国机械工业集团有限公司</p>
              <p class="motto">
                “一颗红心向着党”
              </p>
            </div>
            <img src="../../../static/images/heartList6.jpg" alt="">
          </div>
        </div>
      </div>
      <div class="heartBtn">
        <router-link :to="{path:'/contribute'}">
          <span>我要投稿</span>
        </router-link>
      </div>
    </div>
  </div>

</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        title:'一颗红心',
        defaultIndex:0,
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      choiseffclTab (index){
        this.defaultIndex =index
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var clientWidth =document.documentElement.clientWidth
      if(clientWidth>=750){
        clientWidth =$('body').width()
      }
      var pWidth =clientWidth/4;
      $('.ffclTab p').css('width',pWidth+'px')
      $('.ffclTab').css('min-width', $('.ffclTab p').length*pWidth+'px')

      for(var i=0;i< $('.ffclTab p').length;i++){
        if($('.ffclTab p').eq(i).hasClass('active')){
          $('.ffclScroll').scrollLeft(i*pWidth)
        }
      }
    }
  })
</script>
<style scoped>

  .heartBanner{
    height: 3.6rem;
    position: relative;
  }
  .haertContent{
    background: #f3debc;
    padding:  0 0.32rem;
    padding-top: 0.16rem;
  }
  .haertContent img{
  }
  .heartItem{
    background-image: url("../../../static/images/haertBg2.png");
    background-size: 6.88rem 18.09rem;
    background-repeat: no-repeat;
    height: 18.09rem;
    overflow: hidden;
    padding-bottom: 60px;
  }
  .heartInner{
    padding: 0 0.25rem;
    margin-top: 0.78rem;
  }
  .heartList{
    height: 2.6rem;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border: 0.01rem dashed #dab66a;
    background: #f5eab4;
    vertical-align: top;
    padding: 0.2rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    margin-top: 0.2rem;
  }
  .heartList img{
    width: 2.44rem;
    height: 2.19rem;
    border: 0.01rem dashed #dab66a;
    margin-right: 0.18rem;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }
  .heartList .heartRight{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
    color: #a06801;
  }
  .heartRight .name{
    font-size: 0.32rem;
  }
  .heartRight .joinTime{
    font-size: 0.24rem;
    /* padding-bottom: 0.14rem; */
    /* padding-top: 0.07rem; */
  }
  .position{
    font-size: 0.26rem;
    line-height: 1.5;
  }
  .motto{
    color: #e83329;
    font-weight: bold;
    font-size: 0.3rem;
  }
  .heartBtn,.sendBtn{
    text-align: center;
    padding-bottom: 0.79rem;
  }
  .heartBtn span,.sendBtn span{
    display: inline-block;
    font-size: 0.32rem;
    color: white;
    width: 4.5rem;
    height: 0.7rem;
    text-align: center;
    line-height: 0.7rem;
    background: #e83329;
    border-radius: 0.32rem;
    -webkit-border-radius: 0.32rem;
    margin-top: 0.7rem;
  }
  .heartForm{
    padding: 0.47rem 0.32rem 0.76rem 0.32rem;
  }
  .formItem{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    /*-webkit-box-align: center;*/
    /*-webkit-align-items: center;*/
    /*align-items: center;*/
  }
  .formItem label{
    width: 1.68rem;
    height: 0.7rem;
    vertical-align: middle;
    line-height: 0.7rem;
  }
  .formItem .heartSelect,.heartInput{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
    height: 0.7rem;
    padding-left: 0.83rem;
  }

  .heartTextArea{
    height: 4rem;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
    -webkit-appearance: none;
    apperance:none;
    cursor: pointer;
    border-radius: 0.08rem;
    border: 1px solid #e6e6e6;
  }
  .heartSelect,.heartInput{
    border: 1px solid #e6e6e6;
    border-radius: 0.08rem;
    position: relative;
    margin-bottom: 0.2rem;

  }
  .heartInput{
    padding-right: 0;
  }
  .heartInput input{
    display: inline-block;
    width: 100%;
    height:0.66rem;
    -webkit-appearance: none;
    border: none;
    outline: none;
  }
  .heartSelect:after{
    content: " ";
    display: inline-block;
    border-width: 6px 6px 0 6px;
    border-color:#C8C8CD transparent transparent transparent ;
    border-style: solid;
    position: relative;
    top: -2px;
    position: absolute;
    top: 50%;
    right: 15px;
    margin-top: -4px;
  }
  .formItem .heartSelect select{
    -webkit-appearance: none;
    border: 0;
    outline: 0;
    background-color: transparent;
    width: 100%;
    font-size: inherit;
    height: 100%;
    line-height: 0.7rem;
    position: relative;
    z-index: 1;
    text-align: right;
    color:#C8C8CD ;
  }
  /*select {*/
  /*direction: rtl;*/
  /*}*/
  /*select option {*/
  /*direction: ltr;*/
  /*}*/
  .heartTextArea textarea{
    display: inline-block;
    width: 100%;
    height: 100%;
    -webkit-appearance: none;
    border: 0;
    outline: 0;
    background-color: transparent;
  }
</style>



