<template>
  <div class="newsCenter">
    <Header @modify="modify" @naviBack="naviBack" :title="title" :flag="flag" :title2="title2"></Header>
    <div class="inputDiv">
      <input type="text" name="name" v-model="text">
      <input type="hidden" name="type" value="1">
    </div>
  </div>
</template>
<script>
  import Header from './userheader.vue'
  export default({
    data (){
      return {
        title:'修改信息',
        flag:false,
        title2:'保存',
        text:''
      }
    },
    methods:{
      naviBack(){
        this.$router.go(-1)
      },
      modify (){
        this.$router.push({
          name:'userCenter'
        })
      },
    },
    components:{
      Header
    },
    mounted () {
      this.text =this.$route.query.item
    }
  })
</script>
<style scoped>
  .newsCenter >>> .newsheader .ico2{
    font-size: 0.32rem;
  }
  .newsCenter {
    background: #f6f6f6;
    height: 100%;
  }
  .newsCenter .newsheader .ico-bc{
    font-size: 0.32rem;
  }
  .inputDiv{
    line-height: 0.68rem;
    font-size: 0.28rem;
    color: #333;
    margin: 0 0.32rem;
    margin-top: 0.3rem;
    border-bottom: 0.02rem solid #ccc;
  }
  .inputDiv input{
    display: inline-block;
    width: 100%;
    height: 0.68rem;
    border: none;
    outline: none;
    -webkit-appearance: none;
    cursor: pointer;
    background: #f6f6f6;
  }

</style>



