<template>
  <div class="partItem partItem3">
    <div class="studyTab">
      <p v-for="(item,index) in studyTab" @click.stop="choiseStudyTab(index)" :class="{'active':defaultIndex==index}">
        <span>{{item}}</span>
      </p>
    </div>
    <div class="studyContentTab">
      <div class="studyItem" v-for="(item,index) in studyTab" :class="{'active':defaultIndex==index}">
        <a >
          <router-link :to="{path:'/videoList',query:{id:2}}" >
          <div class="studyList">
            <img src="http://dangjian.co-links.com/Uploads/2018-08-18/5b77bec00a54c.jpg" alt="" class="studyLeft">
            <div class="studyRight">
              <p class="studyTitle">职务犯罪问题研究</p>
              <p class="studyProgress">完成<span style="color: #e83329;">0%</span> | 剩余4课时</p>
              <p class="studyDeadLine">截止至2018-09-03</p>
            </div>
          </div>
          </router-link>
        </a>

      </div>
    </div>
  </div>
</template>
<script>
  export default({
    data (){
        return {
            defaultIndex:0,
            studyTab:['学习中','已完成']
        }
    },
    methods:{
      choiseStudyTab(index){
          this.defaultIndex =index
      }
    }
  })
</script>
<style scoped="">
  .partItem3{
    background: #f9f5f5;
    height: 100%;
  }
  .studyTab{
    height: 0.82rem;
    line-height: 0.82rem;
    font-size: 0;
    background: #ffffff;
  }
  .studyTab p{
    display: inline-block;
    width: 50%;
    text-align: center;
    font-size: 0.32rem;
    color: #999999;
  }
  .studyTab p.active span{
    display: inline-block;
    height: 0.8rem;
    color:#e83329 ;
    border-bottom: 0.02rem solid #e83329;
  }
  .partItem3{
    background: #f9f5f5;
  }
  .studyContentTab{
    margin-top: 0.1rem;
    background: white;
    padding:  0 0.32rem;
  }
  .studyItem{
    display: none;
  }
  .studyItem.active{
    display: block;
  }
  .studyList:nth-child(1){
    padding-top: 0.4rem;
  }
  .studyList{
    padding-bottom: 0.4rem;
    font-size: 0;
  }
  .studyLeft{
    display: inline-block;
    width: 2.4rem;
    height: 1.8rem;
    margin-right: 0.3rem;
    vertical-align: top;
  }
  .studyRight{
    display: inline-block;
    width: 4.1rem;
    vertical-align: top;

  }
  .studyTitle{
    min-height: 0.88rem;
    font-size: 0.32rem;
    color: #333333;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .studyProgress{
    font-size: 0.28rem;
    color: #333333;
    padding-bottom: 0.1rem;

  }
  .studyProgress span{
    color: #e83329;
  }
  .studyDeadLine{
    font-size: 0.22rem;
    color: #999999;
  }
</style>



