<template>
  <div class="ffclIndex">
  <Aside></Aside>
  <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack"></Header>
  <div class="ffcl">
    <div class="ffclScroll">
      <div class="ffclTab">
          <p  v-for="(item,index) in ffclTab" @click.stop="choiseffclTab(index)" :class="{'active':defaultIndex==index}">
            <router-link :to="{name:'ffcl',query:{id:index}}">
               <a >{{item.name}}</a>
            </router-link>
          </p>
      </div>
    </div>
    <!--ffclContent-->
    <div class="ffclContent">
      <div class="ffclContentItem active">
        <router-link :to="{path:'/article',query:{id:2}}"  >
            <a class="ffclItemList" >
              <div class="ffclItemLeft">
                <img src="http://dangjian.co-links.com/Uploads/2018-08-17/5b7647f0a70d7.jpg" alt="">
              </div>
              <div class="ffclItemRight">
                <p class="ffclTitle">
                  国机联讯： 国机集团党委常委、纪委书记雷光华一行巡察中国电工泰国TPI项目</p>
                <p class="ffclDate">
                  <span class="ffclDetailData">2018-08-17 11:58:40</span>
                  <span class="ffclTopic">反腐动态</span>
                </p>
              </div>
            </a>
        </router-link>
      </div>
    </div>
  </div>
  </div>
</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        title:'反腐倡廉',
        defaultIndex:0,
        ffclTab:[
          {
              name:'全部',
              link:''
          },
          {
            name:'反腐动态',
            link:''
          },
          {
            name:'廉政时评',
            link:''
          },
          {
            name:'警钟长鸣',
            link:''
          },
          {
            name:'通报曝光',
            link:''
          }
        ]
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      choiseffclTab (index){
        this.defaultIndex =index
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var clientWidth =document.documentElement.clientWidth
      if(clientWidth>=750){
        clientWidth =$('body').width()
      }
      var pWidth =clientWidth/4;
      $('.ffclTab p').css('width',pWidth+'px')
      $('.ffclTab').css('min-width', $('.ffclTab p').length*pWidth+'px')

      for(var i=0;i< $('.ffclTab p').length;i++){
        if($('.ffclTab p').eq(i).hasClass('active')){
          $('.ffclScroll').scrollLeft(i*pWidth)
        }
      }
    }
  })
</script>
<style scoped>
  .ffcl{
    margin-top: 0.8rem;
  }
  .ffclTab{
    width: 100%;
    height: 0.88rem;
    background: white;
    font-size: 0;
  }
  .ffclTab p{
    display: inline-block;
    width: 25%;
    height: 100%;
    line-height: 0.88rem;
    font-size: 0.32rem;
    color: #999999;
    text-align: center;
  }



  .ffclTab p a{
    color: #999999;
  }
  .ffclTab p.active  a{
    color: #e83329;

  }

  .ffclTab p.active{
    border-bottom: 0.02rem solid #e83329;

  }


  .ffclContent{
    padding: 0.2rem 0.32rem;
    background:#f9f5f5;
  }
  .ffclItemList{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    border-radius: 0.1rem;
    -webkit-border-radius: 0.1rem;
    background: white;
    padding: 0.2rem;
    margin-bottom: 0.2rem;

  }
  .ffclContentItem{
    display: none;
  }
  .ffclContentItem.active{
    display: block;
  }
  .ffclItemLeft,.ffclItemLeft img{
    width: 1.4rem;
    height: 1.4rem;
    margin-right: 0.22rem;
  }
  .ffclItemRight{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
  }
  .ffclTitle{
    font-size: 0.32rem;
    color: #333333;
    vertical-align: top;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    text-align: justify;
  }
  .ffclDate{
    font-size: 0.26rem;
    color: #999;
    padding-top: 0.2rem;
  }
  .ffclTopic{
    float: right;
  }
  .ffclScroll{
    width: 100%;
    overflow-x: scroll;
    height: 0.88rem;
  }
</style>



