<template>
  <div>
    <div class="newsCenter" >
      <Header @modify="modify" @naviBack="naviBack" :title="title" :flag="flag" :flag2="flag2"></Header>
      <div class="newsContent">
        <div class="rankTop">
          <div class="rankLeft">
            <p class="p1">1</p>
            <p class="p2">个人积分</p>
          </div>
          <div class="userAvater">
            <img src="http://pc5hsiop1.bkt.clouddn.com/2018-09-04_5b8e296a04567.png" alt="">
            <p class="userName">15914089352</p>
            <p class="userTitle">恒天</p>
          </div>
          <div class="rankRight">
            <p class="p1">6名</p>
            <p class="p2">个人排名</p>
          </div>
        </div>
        <div class="rankBottom">
          <span
            @click.stop="choiseRank(index)"
            v-for="(item,index) in rankBottom" :class="{'active':defaultIndex1==index}"> {{item}}</span>
        </div>
      </div>
    </div>
      <!---->
      <div class="rankDetail">
        <p class="rankCategory">
          <span    @click.stop="choiseCategory(index)"
            v-for="(item,index) in rankCategory" class="sp1" :class="{'active':defaultIndex2==index}">{{item}}</span>
          <!--<span class="sp1 "> 月榜</span>-->
          <!--<span class="sp2 active">年榜</span>-->
        </p>
        <rankAll :id="defaultId" ></rankAll>
        <loading v-if="showLoading"></loading>
    </div>
  </div>
</template>
<script>
  import Header from '../../components/userheader.vue'
  import rankAll from '../../components/userRank.vue'
  import weui from '../../../static/js/weui.js'
  import loading from '../../components/loading.vue'
  export default({
    data (){
      return {
        showLoading: false,
        title:'我的排名',
        flag:false,
        flag2:true,
        rankBottom:['个人排行','支部排行'],
        rankCategory:['月榜','年榜'],
        defaultIndex1:0,
        defaultIndex2:0,
        defaultId:8
      }
    },
    methods: {
      choiseRank(index){
        this.defaultIndex1 =index
        this.showLoading =true
      },
      choiseCategory (index){
        this.defaultIndex2 =index
        this.showLoading =true
      },
      naviBack(){
        this.$router.go(-1)
      },
      modify (){
        weui.datePicker({
          start: 1989,
          end: new Date().getFullYear(),
          defaultValue: [new Date().getFullYear(), new Date().getMonth() * 1 + 1],
          endMonth: new Date().getMonth() * 1,
          onChange: function (result) {
              console.log(result)
          },
          onConfirm: function (result) {
            var year = result[0];
            var month = result[1];
            var time = year + '-' + month;
          }
        })
      }
    },
    components:{
      Header,
      rankAll,
      loading

    }
  })
</script>

<style scoped="">
  .newsContent{
    height: 3.52rem;
    padding:  0 0.32rem;
    overflow: hidden;
    background-color: #e83329;
    position: relative;
  }
  .rankTop{
    font-size: 0;
    height: 2.6rem;
  }
  .rankTop>div{
    display: inline-block;
    height:2.6rem;
  }
  .rankTop .rankLeft{
    width: 1.8rem;
    padding-top: 0.85rem;
    vertical-align: top;

  }
  .rankTop .userAvater{
    width: 3.22rem;
    padding-top: 0.1rem;
    text-align: center;
  }
  .rankTop .userAvater img{
    display: inline-block;
    width: 1.27rem;
    height: 1.27rem;
    border-radius: 50%;

  }
  .rankTop .rankRight{
    width: 1.8rem;
    vertical-align: top;
    padding-top: 0.85rem;
  }
  .rankTop .p1{
    font-size: 0.28rem;
    color: #f6f3f3;
    text-align: center;
    margin-bottom: 0.2rem;
  }
  .rankTop .p2{
    font-size: 0.24rem;
    color: #f6f3f3;
    text-align: center;
  }
  .userName{
    font-size: 0.32rem;
    color: white;
    margin-top: 0.2rem;
  }
  .userTitle{
    font-size: 0.24rem;
    color: rgba(255,255,255,0.7);
  }
  .rankBottom{
    position: absolute;
    font-size: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.92rem;
    background: rgba(0,0,0,0.3) url("../../../static/images/rankIng.png") no-repeat ;
    background-size: 7.5rem 0.92rem;
    background-position: center;
  }
  .rankBottom span{
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: center;
    line-height: 0.92rem;
    color: rgba(255,255,255,0.4);
    font-size: 0.3rem;
    letter-spacing: 1px;
  }
  .rankBottom span.active{
    color: white;
    position: relative;
  }
  .rankBottom span.active::after{
    position: absolute;
    content: '';
    left: 50%;
    bottom: 0px;
    height: 0px;
    width: 0px;
    border-right: 5px solid transparent;
    border-bottom: 8px solid #fff;
    border-left:5px solid transparent ;
  }
  .rankDetail{
    text-align: center;
  }
  .rankDetail .rankCategory{
    display: inline-block;
    width: 4.86rem;
    height: 0.56rem;
    font-size: 0;
    line-height: 0.56rem;
    border-radius: 0.27rem;
    -webkit-border-radius: 0.27rem;
    margin-top: 0.2rem;
    overflow: hidden;

  }
  .rankCategory span{
    font-size: 0.26rem;
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: center;
    color: white;
    letter-spacing: 3px;

  }
  .rankCategory span{
    background: #f6aba7;
  }
  .rankCategory span.active{

    background: #ed5b53;
  }
</style>



