<template>
  <div id="cc" class="container">
    <img src="../../../static/images/logo.png" alt="" class="logo">
    <div class="itemList" id="itemList">
        <p v-for="(item,index)  in itemList" >
          <router-link :to="{path:'reportSubmit',query:{item:item}}">
               <a >{{item}}</a>
           </router-link>
        </p>
      </div>
  </div>

</template>
<script>
  export default({
    data (){
        return {
            itemList:[
              '公款大吃大喝',
              '公款旅游（国内）',
              '公款出国（境）旅游',
              '违规配备使用公车',
              '违规建设楼堂馆所',
              '津贴补贴违规问题',
              '违规收送礼品礼金',
              '大办婚丧喜庆事宜',
              '接受或用公款参与高消费娱乐健身活动',
              '违规出入私人会所',
              '培训疗养机构“四风”问题'
            ]
        }
    }
  })
</script>
<style scoped="">
  .container{
    overflow: hidden;
    padding-bottom: 0.52rem;
    background-image: url(../../../static/images/banner2.jpg);
    height: 100%;
    background-position: center;
    background-size: 7.5rem 100%;
    background-repeat: no-repeat;
    padding-bottom: 0.52rem;
  }
  .logo {
    position: absolute;
    top: 1.8rem;
    right: 0;
    width: 5.75rem;
    height: 2.84rem;
  }
  .itemList{
    width: 6.88rem;
    margin-left: 0.32rem;
    margin-top:4.17rem ;
    background: white;
    border-radius: 0.16rem
  }
  .itemList p{
    padding-left: 0.25rem;
    padding-right: 0.25rem;
    height: 0.84rem;
    line-height: 0.84rem;
    box-sizing: border-box;
  }
  .itemList p:last-child a{
    border-bottom: none;
  }
  .itemList p>a{
    position: relative;
    display: inline-block;
    width: 100%;
    font-size: 0.28rem;
    color: #333333;
    border-bottom: 1px solid #d9d9d9;
  }
  .itemList p a::after{
    content: " ";
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #C8C8CD;
    border-style: solid;
    -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: relative;
    top: -2px;
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right: 2px;
  }

</style>



