      <div class="weui-dialog__ft">
        <a class="weui-dialog__btn  weui-dialog__btn_default weui_cancel" @click.stop="cancel">取消</a>
        <a class="weui-dialog__btn  weui-dialog__btn_primary weui_confim" @click.stop="confirm">确认</a>
      </div>
    </div>
  </div>
</template>

<script>

  export default({
    data (){
        return {

        }
    },
    props:{
      alarm:{

      }

    },
    methods:{
      cancel(){
        this.$emit('cancel')
      },
      confirm(){
        this.$emit('confirm')
      }
    }

  })
</script>

