let state = {
    desgin: 750,
    device: {
        width: 0,
        height: 0,
    },
    scene: {
        width: 330,
        height: 520
    },
  loginState:{
      isExit:1
  }
}

let getters = {
    ratio(state) {
        return state.scene.width / state.scene.height
    }
}

let mutations = {
    appSetDevice(state, device) {
      console.log(state)
      console.log(device)
        state.device = device
    },
  changeLoginStatus(state,payload){
      console.log(payload)
      state.loginState.isExit =payload.isExit
  }
}

let actions = {}

export default {
    state,
    getters,
    mutations,
    actions
}



