</template>
<script>
  export default({
    data(){
        return{

        }
    },
    props:{
      title:{

      },
      showTitle:{

      }
    },
    methods:{
      toggleMenu(){
        this.$emit('toggleMenu')
      },
      navToBack(){
        this.$emit('navToBack')
      }
    }
  })
</script>



