<template>
  <div class="partNewsIndex">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack">
    </Header>
    <div class="secetion">
      <div class="partTab">
        <div class="partSchool">
          <!--党校视频列表-->
          <div class="partVideoList" >
            <a  class="videoItem" v-for="i in 10" >
              <router-link :to="{path:'/videoList',query:{id:2}}" >
                <p class="videoItemTop">
                  <img src="http://dangjian.co-links.com/Uploads/2018-07-26/5b597687401a8.jpg" alt="">
                  <span>
                                <img src="http://dangjian.co-links.com/Static/Home/images/videoPlay.jpg" alt="">
                 </span>
                </p>
                <p class="videoItemMiddle">党章学习</p>
                <p class="videoItemBottom">
                  <span>75</span>
                </p>
              </router-link>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        nomoreData:false,
        loadmoreClick:false,
        title:'微党校',
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{

      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var swiper =new this.Swiper('.swiper-container',{
        autoplay:true,
        pagination:{
          el:'.swiper-pagination'
        }
      })
    }
  })
</script>
<style scoped="">
  .partTab{
    margin-top: 1.2rem;
  }
  .partNewsIndex{
    width: 100%;
    height: 100%;
    background: #f9f5f5;
  }
  .partNews, .partSchool, .onlineExam {
    padding: 0 0.32rem;
  }
  .videoItem{
    display: inline-block;
    width: 3.3rem;
    padding-bottom: 0.3rem;
  }
  .videoItem:nth-child(odd){
    margin-right: 0.1rem;
  }
  .videoItem:nth-child(even){
    margin-left: 0.1rem;
  }
  .videoItem .videoItemTop{
    position: relative;
    height: 2rem;
  }
  .videoItem .videoItemTop>img{
    display: inline-block;
    width: 100%;
    height: 2rem;

  }
  .videoItem .videoItemTop span {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0.52rem;
    height: 0.52rem;
  }
  .videoItem .videoItemMiddle {
    font-size: 0.3rem;
    color: #333333;
    margin: 0.15rem 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    min-height: 0.6rem;
    height: 0.9rem;
    line-height: 0.45rem;
  }

  .videoItem .videoItemBottom{
    font-size: 0.26rem;
    color: #999999;
    padding-left: 0.5rem;
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .videoItem .videoItemBottom:before{
    position: absolute;
    content: '';
    left: 0;
    top:50%;
    width: 0.36rem;
    height: 0.28rem;
    background-image: url("../../../static/images/eyes.png");
    background-size: 0.36rem 0.28rem;
    background-repeat: no-repeat;
    margin-top: -0.15rem;
  }
</style>



