<template>
  <div class="partNewsIndex">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack">
    </Header>
      <div class="tabs" id="tabs">
        <div class="tabsScroll">
          <div class="tabsHead">
              <p  v-for="(item,index) in tabsHead" @click.stop="choiseHead(index)"  :class="{'active' :defaultIndex ===index}">
                <a >{{item}}</a>
              </p>
          </div>
        </div>
        <div class="blank"> </div>
        <div class="tabContent">
          <div class="tabItem active">
            <div class="swiper-container">
              <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index) in swiperItem">
                  <a >
                    <img :src=item.img alt="" style="width: 375px;" >
                  </a>
                  <p class="swiper-text">
                    <span>{{item.quote}}</span>
                  </p>
                </div>
              </div>
              <!-- 如果需要分页器 -->
              <div class="swiper-pagination"></div>
            </div>
            <div class="newsItemWrap">
              <router-link :to="{path:'article',query:{id:2}}"  tag="div">
              <a class="newsItem" >
                  <div class="newsItemImg">
                    <img src="http://dangjian.co-links.com/Uploads/2018-08-17/5b762c9be0281.jpg" alt="" >
                  </div>
                  <div class="newsItemText">
                    <p class="newsItemTitle">中国自控开展2018年度“七•一”主题党日活动</p>
                    <p class="newsItemContent">6月22日，为庆祝中国共产党成立97周年，进一步深入学习贯彻党的十九大精神，结合中央关于开展“不忘初心牢记使命”主题教育活动的安排，国机集团所属中国自动化控制系统总公司（以下简称“中国自控”）组织“走进伟人寻找初心”七一主题党日活动。公司全系统在职党员、中层以上干部、入党积极分子及团干部共80余人参加活动。　　上午，同志们来到“周恩来邓颖超纪念馆”进行参观，学习周恩来、邓颖超夫妇“为追求真理不懈探索、为民族解放建立功勋、为人民幸福鞠躬尽瘁”的历史贡献。通过实物和影片了解他们在那段峥嵘岁月的工作和生活环境，深深缅怀两位不朽的共产主义战士。周恩来、邓颖超夫妇那不忘初心、无私奉献的坚定信念和将有限的生命投入到无限的事业的崇高人格深深感染了在场的每一个人。　　在主展厅，参加活动的党员同志集体重温入党誓词，面对党旗再次坚定地做出自己的承诺。　　下午，一行人又来到“新中国反腐败第一大案展览馆”。如果说战争时期敌人的武器是“明枪”，那么和平时期就是“暗箭”。在展览馆通过了解刘青山和张子善是如何从革命功臣最终堕落成历史罪人的惨痛教训，使大家时刻谨记“打铁还需自身硬”的深刻道理。</p>
                    <p class="newsItemTime">2018-08-17</p>
                  </div>
               </a>
              </router-link>
            </div>
          </div>
        </div>
      </div>
  <!--加载更多-->
    <div class="loadComponent">
      <div class="weui-loadmore weui-loadmoreIng" v-if="loadmoreClick">
        <i class="weui-loading"></i>
        <span class="weui-loadmore__tips" >正在加载</span>
      </div>
      <div class="weui-loadmore weui-loadmore_line" v-if="nomoreData">
        <span class="weui-loadmore__tips">到底了</span>
      </div>
      <div class="loadMore" cate-id="" style="display:block" @click.stop="loadMore" v-if="!loadmoreClick">
        更多
      </div>
    </div>
    <div id="loadingToast" style="display: none">
      <div class="weui-mask_transparent"></div>
      <div class="weui-toast">
        <i class="weui-loading weui-icon_toast"></i>
        <p class="weui-toast__content">数据加载中</p>
      </div>
    </div>
  </div>
</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        nomoreData:false,
        loadmoreClick:false,
        title:'党建新闻',
        defaultIndex:0,
        swiperItem:[
          {
            query:'',
            img:'http://dangjian.co-links.com/Uploads/2018-08-17/5b762c9be0281.jpg',
            quote:'中国自控开展2018年度“七•一”主题党日活动'
          },
          {
            query:'',
            img:'http://dangjian.co-links.com/Uploads/2018-08-17/5b762b3b33bf7.jpg',
            quote:'中国自控举办学习贯彻党的十九大精神集中轮训班'
          },
          {
            query:'',
            img:'http://dangjian.co-links.com/Uploads/2018-08-17/5b762c9be0281.jpg',
            quote:'轴研科技党总支组织公司党员赴红旗渠参观学习'
          }
        ],
        tabsHead:[
            '全部',
            '时政热点',
            '党建动态'
        ]
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
          this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var swiper =new this.Swiper('.swiper-container',{
        autoplay:true,
        pagination:{
          el:'.swiper-pagination'
        }
      })
    }
  })
</script>
<style scoped="">
  #tabs{
    margin-top: 0.8rem;
    position: relative;
  }
  .tabsHead{
    line-height: 0.88rem;
    height: 0.88rem;
    font-size: 0;
  }
  .tabsHead p{
    display: inline-block;
    width: 25%;
    height: 100%;
    font-size: 0.32rem;

    text-align: center;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }
  .tabsHead p a{
    color: #999999;
  }
  .tabsHead p.active  a{
    color: #e83329;

  }
  .tabsHead p.active {
    border-bottom: 0.02rem solid #e83329;
  }


  .tabContent{
    position:relative;
    height: 100%;

  }
  .tabContent .tabItem{
    height: 100%;
    display: none;
  }
  .tabContent .tabItem.active{
    display: block;
  }
  .swiper-container{
    height: 4rem;
  }
  .swiper-container .swiper-slide{
    height: 4rem;
    position: relative;
  }
  .swiper-container .swiper-slide .swiper-text{
    position: absolute;
    bottom: 0;
    left:0;
    width: 100%;
    height:0.52rem ;
    background: rgba(0,0,0,0.6);
    line-height: 0.52rem;
    z-index: 2;
  }
  .swiper-container .swiper-slide a{
    display: inline-block;
    width: 100%;
    height: 100%;
  }

  .swiper-container .swiper-slide a img{
    width: 375px;
  }

  .swiper-container .swiper-slide .swiper-text span{
    padding-left: 0.32rem;
    font-size: 0.26rem;
    color: #fff;
    display:  inline-block;
    width: 100%;
    padding-right: 1.2rem;
    box-sizing:  border-box;
    text-overflow:  ellipsis;
    white-space:  nowrap;
    overflow:  hidden;
  }

  .swiper-pagination{

  }
  .swiper-container >>> .swiper-pagination-bullet{
    width: 8px;
    height: 8px;
    display: inline-block;
    border-radius: 100%;
    background: rgba(255,255,255,0.6);
    opacity: .2;
  }
  .swiper-pagination{
    text-align: right;
    height: 0.52rem;
    line-height: 0.52rem;
    bottom: 0 !important;
  }
  .swiper-container >>> .swiper-pagination-bullet-active {
    opacity: 1;
    width: 20px;
    height: 8px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    background: rgba(255,255,255) !important;
  }
  .newsItemWrap{
    padding: 0 0.32rem;
  }
  .newsItem .newsItemImg{
    width: 2.45rem;
    height: 1.78rem;
  }
  .newsItem .newsItemImg img{
    width: 2.45rem;
    height: 1.78rem;
  }
  .newsItem .n2{
    padding-right: 0.2rem;
  }
  .newsItemImg{
    width: 2.45rem;
    height: 1.78rem;
  }
  .newsItemTitle,.newsItemContent{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .newsItem .newsItemText .newsItemContent{
    margin-top: 0.05rem;
  }
  .newsItemTime{
    font-size: 0.22rem;
    color: #999999;
  }
  .tabsScroll{
    width: 100%;
    overflow-x: scroll;
    height: 0.88rem;
  }
  .blank{
    height: 0.1rem;
    background: #f9f5f5;
  }
  .newsItem .newsItemText .newsItemContent{
    /* margin-bottom: 0.1rem; */
  }
  .newsItem .newsItemText .newsItemTitle{
    /* margin-bottom: 0.1rem; */
    vertical-align: top;
    height: 0.76rem;
    line-height: 0.38rem;
  }
  .swiper-pagination-bullet{
    opacity: 1;
  }
</style>



