<template>
  <div class="partNewsIndex">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack">
    </Header>
    <div class="activityDetail">
      <div class="detailTitle">
        希望小学募捐    </div>
      <div class="detailDate">
        2018-07-13    </div>
      <div class="detailContent">
        希望小学募捐    </div>
    </div>
    <div class="activityZone">
      <p>活动时间：1970年01月01号</p>
      <p>活动地点：融众国际</p>
      <p> 报名截止时间：1970年01月01号</p>
      <p>报名名额：100人</p>
    </div>
  </div>
</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        nomoreData:false,
        loadmoreClick:false,
        title:'报名详情',
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{

      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var swiper =new this.Swiper('.swiper-container',{
        autoplay:true,
        pagination:{
          el:'.swiper-pagination'
        }
      })
    }
  })
</script>
<style scoped="">
  .activityDetail{
    padding: 0 0.32rem;
    margin-top: 1rem;
  }
  .detailTitle{
    font-size: 0.32rem;
    color: #333333;
    text-align: center;
    padding-top: 0.4rem;
    padding-bottom: 0.1rem;
  }
  .detailDate{
    text-align: center;
    font-size: 0.26rem;
    color: #999999;

  }
  .detailContent p{
    text-indent: 2em;
    color: #333;
    font-size: 14px;
    line-height: 20px;
    text-align: justify;
    margin-top: 12px;
  }

  .detailContent .img{
    text-align: center;
    max-width: 100%;
    text-indent: 0;

  }
  .activityZone{
    margin: 0.34rem;
    border: 1px solid #e83329;
    border-radius: 0.15rem;
    padding: 0.3rem 0 0.3rem 0.4rem;
  }
  .activityZone p{
    font-size: 0.26rem;
    color: #333;
    line-height: 1.8;
  }
  .sendBtn{
    text-align: center;
    margin-top: 0.5rem;

    margin-bottom: 0.9rem;
  }
  .sendBtn span {
    display: inline-block;
    font-size: 0.32rem;
    color: white;
    width: 4.5rem;
    height: 0.7rem;
    text-align: center;
    line-height: 0.7rem;
    background: #999999;
    border-radius: 0.32rem;
    -webkit-border-radius: 0.32rem;
    margin-top: 0.7rem;
  }
</style>



