对党旗再次坚定地做出自己的承诺。　　下午，一行人又来到“新中国反腐败第一大案展览馆”。如果说战争时期敌人的武器是“明枪”，那么和平时期就是“暗箭”。在展览馆通过了解刘青山和张子善是如何从革命功臣最终堕落成历史罪人的惨痛教训，使大家时刻谨记“打铁还需自身硬”的深刻道理。")]),_vm._v(" "),_c('p',{staticClass:"newsItemTime"},[_vm._v("2018-08-17")])])])])],1)])])]),_vm._v(" "),_c('div',{staticClass:"loadComponent"},[(_vm.loadmoreClick)?_c('div',{staticClass:"weui-loadmore weui-loadmoreIng"},[_c('i',{staticClass:"weui-loading"}),_vm._v(" "),_c('span',{staticClass:"weui-loadmore__tips"},[_vm._v("正在加载")])]):_vm._e(),_vm._v(" "),(_vm.nomoreData)?_c('div',{staticClass:"weui-loadmore weui-loadmore_line"},[_c('span',{staticClass:"weui-loadmore__tips"},[_vm._v("到底了")])]):_vm._e(),_vm._v(" "),(!_vm.loadmoreClick)?_c('div',{staticClass:"loadMore",staticStyle:{"display":"block"},attrs:{"cate-id":""},on:{"click":function($event){$event.stopPropagation();return _vm.loadMore($event)}}},[_vm._v("\n      更多\n    ")]):_vm._e()]),_vm._v(" "),_vm._m(0)],1)}
