<template>
  <div class="partItem2">
    <div class="anthologyLeft">
      <div class="anthologyList" v-for="(item,index) in anthologyList" @click.stop="choiseCourse(index)" :class="{'active':defaultIndex===index}">
        {{item}}
      </div>
    </div>
    <div class="anthologyRight">
      <div class="anthologyRightItem" v-for="(item,index) in anthologyRightItem" :class="{'active':defaultIndex===index}">
        <a  v-for="(item2,index) in item.items" >
          <router-link :to="{path:'/selectCourse',query:{id:2}}" >
            <div class="anthologyImgList">
              <img :src=imgBg alt="">
              <p>{{item2.name}}</p>
            </div>
          </router-link>
        </a>
      </div>
    </div>
  </div>
</template>
