  export default({
    data(){
        return{
          imgBg:'http://dangjian.co-links.com/Static/Home/images/partSchollItem1.jpg',
          anthologyList:[
              '两学一做',
              '思想理论',
            '党性教育',
            '形势教育',
            '政策法规'
          ],
          anthologyRightItem:[
            {
              items:[
                {
                    name:'全部',
                    link:''
                },
                {
                  name:'学党章',
                  link:''
                },
                {
                  name:'学党规',
                  link:''
                },
                {
                  name:'做合格党员',
                  link:''
                }
              ]
            },
            {
              items:[
                  {
                    name:'全部',
                    link:''
                  },
                  {
                    name:'中国梦',
                    link:''
                  },
                {
                  name:'“四个全面”战略布局',
                  link:''
                },
                {
                  name:'五大发展理念',
                  link:''
                },
                {
                  name:'中国特色社会主义',
                  link:''
                }
                  ]
            },
            {
              items:[
                {
                  name:'全部',
                  link:''
                },
                {
                  name:'党风廉政建设',
                  link:''
                },
                {
                  name:'党史知识',
                  link:''
                },
                {
                  name:'三严三实',
                  link:''
                }

              ]
            },
            {
              items:[
                {
                  name:'全部',
                  link:''
                },
                {
                  name:'“十三五”规划',
                  link:''
                },
                {
                  name:'经济形势',
                  link:''
                }
              ]
            },
            {
              items:[
                {
                  name:'全部',
                  link:''
                },
                {
                  name:'政策解读',
                  link:''
                },
                {
                  name:'法律法规',
                  link:''
                }
              ]
            }
          ],
          defaultIndex:0
        }
    },
    methods:{
      choiseCourse(index){
          this.defaultIndex  =index
      }
    }
  })
</script>
<style scoped="">
   .partItem2 {
    position: relative;
    height: 100%;
  }
   .anthologyLeft{
     display: inline-block;
     width: 2.5rem;
     position: absolute;
     left: 0;
     top:0;
     bottom: 0;
     background: #f9f5f5;
   }
   .anthologyList{
     font-size: 0.32rem;
     color: #000000;
     text-align: center;
     line-height: 0.86rem;
     height: 0.86rem;
     width: 100%;
   }
   .anthologyList.active{
     color: #e83329;
     border-bottom: 1px solid #e83329;
     background: white;
   }
   .anthologyRight{
     padding-top: 0.3rem;
     display: inline-block;
     width: 5rem;
     background: white;
     position: absolute;
     left: 2.5rem;
     right: 0;
     top:0;
     bottom: 0;
     padding: 0 0.32rem;
     padding-top: 0.3rem;
     background: white;
     box-sizing: border-box;
     -webkit-box-sizing: border-box;
   }
   .anthologyImgList{
     position: relative;
     display: inline-block;
     width: 2rem;
     height: 2rem;
     text-align: center;
     line-height: 2rem;
     font-size: 0.32rem;
     color: white;

     padding-bottom: 0.3rem;
   }
   .anthologyImgList:nth-child(odd){
     margin-right: 0.1rem;
   }
   .anthologyImgList:nth-child(even){
     margin-left: 0.1rem;
   }
   .anthologyImgList p{
     position: absolute;
     left: 0;
     top:0;
     width: 100%;
     height: 100%;
   }
   .anthologyRightItem{
     display: none;
   }
   .anthologyRightItem.active{
     display: block;
   }
</style>



