<template>
    <div class="partAlbumIndex">
      <Aside></Aside>
      <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack"></Header>
      <div class="albumContent">
        <div class="albumList">
          <p class="albumTitle">
            <a href="">习近平在武汉考察</a>
          </p>
          <p class="albumTime">
            <span>2018-08-17 17:18:29</span>
          </p>
          <div class="albumImg clearfix">
            <div class="left">
              <img src="http://dangjian.co-links.com/Uploads/2018-08-17/5b7692e582ecb.jpg" alt="">
            </div>
            <div class="right">
              <p>
                <img src="http://dangjian.co-links.com/Uploads/2018-08-17/5b7692e583299.jpg" alt="">
              </p>
              <p>
                <img src="http://dangjian.co-links.com/Uploads/2018-08-17/5b7692e5835ed.jpg" alt="">
                <router-link :to="{name:'albumDetail',query:{id:1}}">
                  <a><span>更多</span></a>
                </router-link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
        return{
          title:'党建相册'
        }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      choiseffclTab (index){
        this.defaultIndex =index
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var clientWidth =document.documentElement.clientWidth
      if(clientWidth>=750){
        clientWidth =$('body').width()
      }
    }
  })
</script>
<style scoped>
  .albumContent{
    padding:  0 0.32rem;
    margin-top: 0.8rem;
  }
  .albumList{
    padding-top: 0.3rem;
    padding-bottom: 0.26rem;
  }
  .albumTitle{
    padding-bottom: 0.1rem;
  }
  .albumTitle a{
    font-size: 0.32rem;
    color: #333333;
    font-weight: bold;
  }
  .albumTime {
    font-size: 0.26rem;
    color: #999999;
  }
  .albumTime  span{
    margin-right: 0.3rem;
  }
  .albumImg{
    height:2.7rem ;
    margin-top: 0.2rem;
  }
  .albumImg .left{
    width: 4.25rem;
    height: 2.67rem;
  }
  .albumImg .right{
    width: 2.5rem;
    height: 100%;
  }
  .albumImg .right img{
    width: 2.5rem;
  }
  .albumImg .right p:nth-child(1) img{
    display: inline-block;
    max-width:100% ;
    vertical-align: top;
    height: 1.3rem;
    margin-bottom: 0.1rem;
  }
  .albumImg .right p:nth-child(2) img{
    max-width:100% ;
    vertical-align: bottom;
    height: 1.3rem;
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
  }
  .albumImg .right p{
    position: relative;
  }
  .albumImg .right p span{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    /*background: rgba(255,255,255,0.8);*/
    font-size: 0.32rem;
    color: white;
    font-weight: bold;
    text-align: center;
    line-height: 1.3rem;

  }
</style>



