<template>
  <div class="newsheader flexs">
    <a @click.stop="naviBack"> <span class="iconfont icon-left1 ico1"></span></a>
    <p class="headerText">{{title}}</p>
    <span class="iconfont  ico2" @click.stop="modify" :flag="flag" :class="{'icon-bianji3': flag,'icon-calender':flag2}">{{title2}}</span>
  </div>
</template>

<script>
  export default({
    data (){
        return {

        }
    },
    props:{
        title:{

        },
      flag:{

      },
      flag2:{

      },
      title2:{

      }
    },
    methods:{
      naviBack(){
          this.$emit('naviBack')
      },
      modify (){
          this.$emit('modify')
      }
    }
  })
</script>
<style scoped="">
  .flexs{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
  }
  .newsheader{
    padding: 0 0.32rem;
    height: 0.8rem;
    line-height: 0.8rem;
    justify-content: space-around;
    background: #e83329;
  }
.newsheader .headerText{
    font-size: 0.32rem;
    color: white;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
    text-align: center;
  }
 .newsheader .iconfont{
    font-size :0.36rem;
    color:white;
    width: 0.8rem;
    height: 100%;

  }
 .newsheader .ico1{
    text-align: left;
  }
 .newsheader .ico2{
    text-align: right;
  }
</style>



