<template>
  <div class="partNewsIndex">
    <Aside></Aside>
    <Header :title="title" @toggleMenu="toggleMenu" @navToBack="navToBack">
    </Header>
    <div class="secetion">
      <div class="videoWrap">
        <div class="videoPlayer ">
          <video
            webkit-playsinline="webkit-playsinline"
            x-webkit-airplay="isHtml5UseAirPlay"
            playsinline="" x5-playsinline=""
            autoplay=""
            controls=""
            src="http://pc5hsiop1.bkt.clouddn.com/051%20%E8%BF%90%E5%8A%A8%E9%9E%8B%E6%B5%B7%E6%8A%A5.mp4"
            id="video" style="object-fit:fill"
            preload="auto" currenttime="0"
            poster="/Uploads/2018-08-15/5b73ec03cbf78.jpg"
            chapter-id="9">
          </video>
        </div>
        <div class="videoTab">
          <p @click.stop="selectItem(index)" v-for="(item,index) in videoTab" :key="index" :class="{'active':defaultIndex==index}">
            <span>{{item}}</span>
          </p>
        </div>
        <div class="videoContentTab">
          <div class="videoContent " v-if="defaultIndex==0">
            <div class="videoItem">
              <div class="title clearfix" id="test">
                <p class="left">
                  十九大
                </p>
              </div>
              <router-link :to="{path:'/videoList',query:{id:1}}"  v-for="i in 10" :key="i">
                  <div class="videoList">
                    <a >
                      <div class="videoLeft" >
                        <p class="videoTotalSum" style="color: #e83329;">
                          第一章 十九大</p>
                        <p class="videoTime">0分/4分21秒  </p>
                      </div>
                    </a>
                    <div class="videoRight">
                      <p>未完成</p>
                    </div>
                  </div>
              </router-link>
            </div>
          </div>
          <div class="videoContent" v-else>
            <div class="videoItem">
              <div class="title clearfix">
                <p class="left">十九大 </p>
              </div>
              <div class="videoConentInto">
                十九大精神
              </div>
            </div>
          </div>
        </div>
      </div>
    <div class="addBtn">
      <span id="addBt2">加入学习计划</span>
    </div>
  </div>
  </div>
</template>
<script >
  import aside from '../../components/aside.vue'
  import header from '../../components/header.vue'
  export default({
    data (){
      return {
        nomoreData:false,
        loadmoreClick:false,
        title:'微党校',
        defaultIndex:0,
        videoTab:[
            '目录',
            '课程详情'
        ]
      }
    },
    components:{
      Aside:aside,
      Header:header
    },
    methods:{
      selectItem(index){
        this.defaultIndex =index
      },
      navToBack(){
        this.$router.go(-1);//返回上一层
      },
      toggleMenu(){
        $('.aside').toggleClass('active')
        $('body').toggleClass('active')
        if($('.aside').hasClass('active')){
          $('#toggleBtn').find('.img2').hide()
          $('#toggleBtn').find('.img1').show()
        }else{
          $('#toggleBtn').find('.img1').hide()
          $('#toggleBtn').find('.img2').show()
        }
      },
      loadMore (){
        this.loadmoreClick =!this.loadmoreClick
      },
      choiseHead(index){
        this.defaultIndex =index
      }
    },
    mounted (){
      $('body').removeClass('active')
      var swiper =new this.Swiper('.swiper-container',{
        autoplay:true,
        pagination:{
          el:'.swiper-pagination'
        }
      })
    },
    beforeDestroy(){
        console.log(this.$el.querySelectorAll('video')[0].currentTime)
    },
  })
</script>
<style scoped="">
  .partNewsIndex{
    width: 100%;
    height: 100%;
    background: #f9f5f5;
  }
  body{

  }
  .videoPlayer{
    height:4.2rem;
    margin-top: 0.8rem;
    position: relative;
  }
  .videoWrap{
    background: #f9f5f5;
    height: calc( 100% - 0.92rem );
    overflow: scroll;
  }
  .videoWrap video{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .videoWrap  img{
    position: absolute;
    left: 50%;
    top:50%;
    margin-top: -0.44rem;
    margin-left: -0.44rem;
    width: 0.88rem;
    height: 0.88rem;
  }

  .videoTab{
    height: 0.8rem;
    line-height: 0.8rem;
    font-size: 0;
    background: #ffffff;
    border-bottom: 0.02rem solid #f9f5f5;
  }
  .videoTab p{
    display: inline-block;
    width: 50%;
    text-align: center;
    font-size: 0.32rem;
    color: #999999;
  }
  .videoTab p.active span{
    display: inline-block;
    height: 0.8rem;
    color:#e83329 ;
    padding: 0 0.3rem;
    border-bottom: 0.02rem solid #e83329;
  }
  .title{
    height: 1rem;
    line-height: 1rem;
    border-bottom: 0.02rem solid #f9f5f5;
  }
  .title .left{
    font-size: 0.32rem;
    color: #333333;
    position: relative;
    padding-left: 0.63rem;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 0.9rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;


  }
  .title .left:after{
    position: absolute;
    content: '';
    left: 0;
    top:50%;
    width: 0.45rem;
    height: 0.41rem;
    background-image: url("../../../static/images/star.png");
    background-size: 0.45rem 0.41rem;
    background-repeat: no-repeat;
    margin-top: -0.2rem;

  }
  .title .right{
    font-size: 0.26rem;
    color: #e32b2c;
  }
  .videoList{
    padding-left: 0.63rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    justify-content: space-between;
    padding-top: 0.2rem;
    padding-bottom: 0.15rem;
  }
  .videoLeft .videoTotalSum{
    font-size: 0.28rem;
    color: #333333;

  }
  .videoLeft .videoTime{
    color: #999999;
    font-size: 0.22rem;
  }
  .videoRight p{
    font-size: 0.26rem;
    color: #999;
    width: 1rem;
    margin-left: 0.2rem;
  }
  .addBtn,.addBtn2{
    position: fixed;
    bottom: 0.2rem;
    left: 0;
    width: 100%;
    text-align: center;
    /*margin: 0.46rem 0 0.46rem 0;*/

  }
  .videoItem{
    padding: 0 0.32rem;
    background: #fff;
  }
  .addBtn span,.addBtn2 span{
    display: inline-block;
    width: 5.3rem;
    height: 0.72rem;
    line-height: 0.72rem;
    background: #e83329;
    font-size: 0.32rem;
    color: white;
    border-radius: 0.3rem;
    -webkit-border-radius: 0.3rem;
  }
  .addBtn2 span{
    background: #999999;
  }

  .videoConentInto{
    padding-top: 0.3rem;
    padding-bottom: 0.2rem;
  }
  .videoConentInto p{
    text-indent: 2em;
    font-size: 0.26rem;
    color: #333333;
    padding-bottom: 0.2rem;
    text-align: justify;
  }

</style>



